.vertical-nav {
    min-width: 17rem;
    width: 17rem;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
    transition: all 0.4s;
    overflow-y: auto;
}

.vertical-nav .nav .nav-item:hover {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.vertical-nav ul li {
  font-size: 16px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
  
.page-content {
  width: calc(100% - 17rem);
  margin-left: 17rem;
  transition: all 0.4s;
}

#sidebar.active {
  margin-left: -17rem;
}

#content.active {
  width: 100%;
  margin: 0;
}

@media (max-width: 768px) {
  #sidebar {
    margin-left: -17rem;
  }
  #sidebar.active {
    margin-left: 0;
  }
  #content {
    width: 100%;
    margin: 0;
  }
  #content.active {
    margin-left: 17rem;
    width: calc(100% - 17rem);
  }
}

  
body {
background: #599fd9;
background: -webkit-linear-gradient(to right, #599fd9, #c2e59c);
background: linear-gradient(to right, #599fd9, #c2e59c);
min-height: 100vh;
overflow-x: hidden;
}

.separator {
margin: 3rem 0;
border-bottom: 1px dashed #fff;
}

.text-uppercase {
letter-spacing: 0.1em;
}


#sidebarCollapse {
    color: #5a5a5a;
}

#sidebarCollapse:active
#sidebarCollapse:focus {
    outline: none;
    color: #5a5a5a;
}

/* Шрифты */

.vertical-nav .nav .nav-item .nav-link {
  font-family: Roboto;
  font-weight: 500;
  color:rgba(0, 0, 0, 0.8);
}


.vertical-nav .text-gray {
  color: rgba(0, 0, 0, 0.7);
  font-family: Roboto;
  font-size: 18px;
}
